@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Intro__headline,.Intro__subheadline,.Intro__image,.Intro__paragraph{margin-bottom:20px;text-align:center}

.Question{height:100vh;-webkit-justify-content:space-between;justify-content:space-between}.Question__progress{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;height:50px;-webkit-justify-content:space-between;justify-content:space-between;width:100%}.Question__progress__back{color:#131429;font-size:25px;background:transparent;padding:unset}.Question__label{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:20px}.Question__label input{padding:16px}.Question__label label{margin-bottom:20px;text-align:center}.Question__image{margin-bottom:20px}.Question__button.-disabled{background-color:#9d9fd1}.Question__radio{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.Question__radio p{text-align:center}.Question__radio-item{display:inline-block;margin:10px 0 0;padding:0 6px;position:relative}.Question__radio-item input[type=radio]:checked+label:after{border-radius:11px;width:12px;height:12px;position:absolute;top:11px;left:12px;content:" ";display:block;background:#004c97}.Question__radio-item input[type='radio']{display:none}.Question__radio-item label{font-weight:normal}.Question__radio-item label:before{content:" ";display:inline-block;position:relative;top:5px;margin:0 20px 0 0;width:20px;height:20px;border-radius:11px;border:2px solid #004c97;background-color:transparent}.Question-1 .Question__progress a{visibility:hidden}

.Elaborating__headline{margin-bottom:80px}.Elaborating__headline span{text-transform:capitalize}.Elaborating__spinner{display:inline-block;width:80px;height:80px;margin:40px 0}.Elaborating__spinner::after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:4px solid #131429;border-color:#131429 transparent;-webkit-animation:spinner 3s linear infinite;animation:spinner 3s linear infinite}@-webkit-keyframes spinner{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinner{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.Modal{background:rgba(0,0,0,0.65);-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);height:100%;position:fixed;top:0;left:0;transition:all .4s .2s ease-in-out;width:100%;z-index:1000}.Results{height:auto;padding-left:0 !important;padding-right:0 !important}.Results__headline,.Results__subheadline{margin-bottom:20px}.Results__songs{display:-webkit-flex;display:flex;list-style:none;-webkit-flex-wrap:wrap;flex-wrap:wrap}.Results__songs .Results__song{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:20px;width:50%;padding:0 20px;text-align:center}.Results__songs .Results__song-image{height:130px}.Results__songs .Results__song figure img{border-radius:6px;height:120px;width:120px}.Song{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;z-index:9999;position:fixed;background:white;height:400px;top:calc(50% - 200px);-webkit-justify-content:space-evenly;justify-content:space-evenly;border-radius:6px;padding:30px}.Song__image img{border-radius:6px;height:25vh;margin-bottom:20px}.Song__buttons{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.Song__buttons .Song__play,.Song__buttons .Song__stop{width:50%}.Song__buttons .Song__change{margin:20px 0}

*{box-sizing:border-box;margin:0;padding:0}body,html{height:100%;max-width:100%}html{font-size:100%}body::-webkit-scrollbar{display:none}body{background-color:#ececec;color:#131429;font-family:'Poppins', sans-serif}body{-ms-overflow-style:none;scrollbar-width:none}body.blocked{overflow-y:hidden}section{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;padding:15vw}button{background-color:#131429;color:#ececec;border:0;border-radius:6px;font-family:'Poppins', sans-serif;padding:10px 20px}img{max-width:100%;max-height:100%}h1{font-size:28px}h2{font-size:22px}h3{font-size:20px}h1,h2,h3{text-align:center}div,p,input{font-size:16px}.App{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}

